body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: #75bbba;
  background-color: #d3f0ef;
}

.header {
  width: 100%;
  height: 250px;
  background-color: #68aaa5;
}
.header.FM {
  background-image: url("./component/images/bg-header-mobile.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.header.FP {
  background-image: url("./component/images/bg-header-desktop.svg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}/*# sourceMappingURL=App.css.map */